import { Component } from '@angular/core';
import { ViewController,IonicPage,  NavController,Events , ActionSheetController, NavParams } from 'ionic-angular';
import {   FormBuilder,  FormGroup} from '@angular/forms';
import { Auth,NotificationProvider } from '../../../providers';
import { TranslateService } from '@ngx-translate/core';
import { Storage } from '@ionic/storage';
@IonicPage()
@Component({
  selector: 'page-suscriptorsaldo',
  templateUrl: 'suscriptorsaldo.html',
})
export class SuscriptorsaldoPage {

  
  public form                  : FormGroup;
  username: string = "";
  
  public idsuscriptor                 : any;
  public idnombre                 : any;
  idempresa:any;
  public saldoactual                 : any;  
  public codigo1                 : any;
  public codigo2                 : any;
  public codigo3                 : any;
  public codigo4                 : any;
  public codigo5                 : any;

  public dispositivo1                 : any;
  public dispositivo2                 : any;
  public dispositivo3                 : any;
  public dispositivo4                 : any;
  public dispositivo5                 : any;
 
  public vgrupo  :any=[];
  private _ID                  : String;

  public pageTitle           	: string;
  createdCode = null;
  scannedCode = null;
  public card: any= {
    state: 'OFF',
    logo: "assets/img/visa2.png",
    a: 1234,
    b: 5522,
    c: 8432,
    d: 2264,
    expires: '7/12',
    bank: 'Tarjeta'
  };   
  

  isDisablednit :any;
  constructor( public storage: Storage,  public msj: NotificationProvider, public translateService: TranslateService,public viewCtrl: ViewController,public authService:  Auth,public navCtrl 		: NavController,  
    public navParams 	: NavParams,   private _FB          : FormBuilder,         public actionSheetCtrl: ActionSheetController, 
          public events: Events)
  {

    
     // Define form validation - VERY basic!
     this.form = this._FB.group({
      saldoactual       : [''],
      codigo1       : [''],
      codigo2       : [''],
      codigo3       : [''],
      codigo4       : [''],
      codigo5       : [''],
       dispositivo1 : [''],
       dispositivo2 : [''],
       dispositivo3 : [''],
       dispositivo4 : [''],
       dispositivo5 : ['']
     });


    
   
     this.authService.getReg('suscriptor-disp','datosfijos2').then((result) => {
      this.vgrupo=result;
    }, (err) => {  
    this.msj.showError3('Error',err.error);
  });


  }

  ngOnInit() {
    this.storage.get('username').then((value) => {
      this.username = value;
  }); 
  }

  ionViewDidLoad() : void
  {
    
    this.idsuscriptor=this.navParams.data.record2.idsuscriptor;
    this.idnombre=this.navParams.data.record2.idnombre;
    this.translateService.get('APPX').subscribe((value) => {
      this.idempresa=value

   
  
     
  })

    this.authService.getReg(this.navParams.data.record2.iddpi+'/personadisp/' + this.idempresa,'personals').then((result) => {
      

      this._ID 				=	result[0]._id;
      this.saldoactual		=	result[0].saldo;
      this.codigo1		=	result[0].codigo1;
      this.codigo2		=	result[0].codigo2;
      this.codigo3		=	result[0].codigo3;
      this.codigo4		=	result[0].codigo4;
      this.codigo5		=	result[0].codigo5;
      this.dispositivo1		=	result[0].dispositivo1;
      this.dispositivo2		=	result[0].dispositivo2;
      this.dispositivo3		=	result[0].dispositivo3;
      this.dispositivo4		=	result[0].dispositivo4;
      this.dispositivo5		=	result[0].dispositivo5;


    }, (err) => {  
    this.msj.showError3('Error',err.error);
  });


  this.pageTitle="Dispositivos y saldo";

    if(this.navParams.get("record")==null)
    {
   
      
        
     

     
      this.isDisablednit =true
    }
    else {
     

      
       this.isDisablednit =false
      
    

    }
  }


 
  

  manageGallery() : void
  {
    
  let creat='Actualiza';
    if(this.navParams.get("record")==null)
    {  creat='Crea';
    }
   


     let   options       : any	     = { 
      idsuscriptor : {id:this.idsuscriptor,nombre:this.idnombre},  
      codigo1 : this.form.controls['codigo1'].value,
      codigo2 : this.form.controls['codigo2'].value,
      codigo3 : this.form.controls['codigo3'].value,
      codigo4 : this.form.controls['codigo4'].value,
      codigo5 : this.form.controls['codigo5'].value,
     
      dispositivo1 : this.form.controls['dispositivo1'].value,
      dispositivo2 : this.form.controls['dispositivo2'].value,
      dispositivo3 : this.form.controls['dispositivo3'].value,
      dispositivo4 : this.form.controls['dispositivo4'].value,
      dispositivo5: this.form.controls['dispositivo5'].value,
   saldoactual: this.form.controls['saldoactual'].value,
     bitacora:{idempresa:this.idempresa,idafiliado:'',email: this.username,permiso:creat,accion:creat+' suscriptor saldo '}
    };


     if(this.navParams.get("record")==null)
     {  
          if(options){
            this.authService.createReg('',options,'suscriptorsaldos').then((result) => {
                 if (!this.form.valid) { return; }
                 this.viewCtrl.dismiss(result);
         
          }, (err) => {  
            this.msj.showError3('Error',err.error);
          });
          }
     }   
     else
     {
      if(options){
        this.authService.createReg(this._ID,options,'suscriptorsaldos').then((result) => {
           if (!this.form.valid) { return; }
           this.viewCtrl.dismiss(result);
       }, (err) => {
        this.msj.showError3('Error',err.error);
       });
    }}
  }
  stringify(country){
    return JSON.stringify(country);
  }

  cancel() {
    this.viewCtrl.dismiss();
  }
  done() {
    if (!this.form.valid) { return; }
    //this.viewCtrl.dismiss(this.form.value);
  }
  optionsFn(item) {//here item is an object 
    console.log(item);
  }


}



